* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
  position: fixed;
  height: 100%;
  width: 100%;
  background: linear-gradient(to right, #bcbcbc, #eaeaea);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 80%;
  height: 100%;
  margin: auto;
  padding: 50px;
  box-sizing: border-box;

}

.header {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 20px;

}

.header_box {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
}

.header_box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.nameUser {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.nameUser samp {
  font-size: 30px;
  font-weight: bold;
  font-family: 'Courier New', Courier, monospace;
}
.nameUser span::before{
  content: "(";
}
.nameUser span::after{
  content: ")";
}
.contact {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
}

.contact_box {
  background-color: rgb(255, 255, 255);
  width: 90%;
  min-height: 50px;
  margin-bottom: 20px;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.2s;
}

.contact_box:hover {
  transform: scale(1.05);
  transition: 0.2s;
}

.contact_box:active {
  transform: scale(0.99);
}

.contact_box a {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gg-facebook,
.gg-twitter {
  --ggs: 1.5
}



.title samp {
  font-size: 20px;
}

a {
  text-decoration: none;
}

.box_hidden {
  display: none;
}

.viewImage {
  position: fixed;
  background-color: #818181c5;
  height: 100%;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.viewImage_box {
  width: 550px;
  height: 650px;
  border-radius: 15px;
  overflow: hidden;
}

.viewImage_box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Thiết bị di động */
@media screen and (max-width: 480px) {

  .viewImage_box {
    width: 350px;
    height: 450px;
  }
}

/* Thiết bị bảng */
@media screen and (min-width: 481px) and (max-width: 768px) {
  .viewImage_box {
    width: 450px;
    height: 550px;
  }
}
